body {
  margin-top: 39px !important;
}

@media (max-width: 582px) {
  body {
    margin-top: 52px !important;
  }
}

#share-draft-content-message {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  height: 39px;
  padding: 13px 0;
  background-color: #fff;
  box-sizing: border-box;
  z-index: 999999;
}

@media (max-width: 582px) {
  #share-draft-content-message {
    height: 52px;
  }
}

#share-draft-content-message .extra-information {
  color: #333;
  width: 300px;
  position: absolute;
  background-color: #fff;
  display: none;
  text-align: left;
  padding: 8px;
  -webkit-box-shadow: 0 -1px 4px #aaa;
  -moz-box-shadow: 0 -1px 4px #aaa;
  box-shadow: 0 -1px 4px #aaa;
  top: 24px;
  right: 0;
}

#share-draft-content-message .extra-information:after {
  content: '';
  position: absolute;
  top: -5px;
  width: 10px;
  height: 10px;
  background: #fff;
  right: 10%;
  -webkit-box-shadow: -1px -1px 2px #aaa;
  -moz-box-shadow: -1px -1px 2px #aaa;
  box-shadow: -1px -1px 2px #aaa;
  -moz-transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

#share-draft-content-message .extra-information li {
  margin: 8px 0 0 16px;
  list-style: disc;
}

#share-draft-content-message .extra-information li:first-child {
  margin: 0;
  list-style: none;
}

#share-draft-content-message .message {
  text-align: center;
  margin-top: 0;
  margin-bottom: 0;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 13px;
  line-height: 16px;
  color: #333;
}

#share-draft-content-message span {
  color: #0b7aca;
  position: relative;
}

@media (max-width: 768px) {
  #share-draft-content-message span {
    display: none;
  }
}

#share-draft-content-message span:hover .extra-information {
  display: block;
}

#share-draft-content-message em {
  font-weight: bold;
}




